import React from 'react'

function TodoInput() {
    return (
        <div>
            <h3>To do Input!!!</h3>
            <div className="row m-2">
                <input className="col form-control" type="text"/>
                <button className="btn btn-primary mx-2">Submit</button>
            </div>
        </div>
    )
}

export default TodoInput
