import React from 'react'

function TodoItem() {
    return (
        <div>
            <div className="row mx-3 align-items-center">
                <div>#1</div>
                <div className="col">
                    <h4>Todo Title</h4>
                </div>
                <button class="btn btn-warning m-2">Edit</button>
                <button class="btn btn-danger m-2">Delete</button>
            </div>
        </div>
    )
}

export default TodoItem
